import { h } from "preact";
import { useMemo } from "preact/hooks";
import { Paragraph } from "../editorState";
import style from "./preview.css";
import cardStyle from "../../card/card.css";
import jpStyle from "../../styles/japanese.css";
import { JSXInternal } from "preact/src/jsx";

export type PreviewProps = {
  list: string[];
  paragraph: Record<string, Paragraph>;
};

export default function Preview(props: PreviewProps) {
  const text = useMemo(() => {
    return props.list.map((id) => {
      const lines = (props.paragraph[id]?.text ?? "").split("\n");
      const text = lines.reduce<(JSXInternal.Element | string)[]>(
        (acc, text, i) => {
          if (i === lines.length - 1) {
            return [...acc, text];
          } else {
            return [...acc, text, <br key={`br-${i}`} />];
          }
        },
        []
      );
      return (
        <p
          className={`${jpStyle["japanese-text"]}`}
          key={`preview-paragraph-${id}`}
        >
          {text}
        </p>
      );
    });
  }, [props.list, props.paragraph]);
  return <div className={`${style.preview} ${cardStyle.card}`}>{text}</div>;
}
