import { createContext } from "preact";

export type Paragraph = {
  id: string;
  text: string;
};

export type EditorState = {
  ver: number;
  paragraphs: Record<string, Paragraph>;
  list: string[];
  lastAdded: number | null;
  isSorting: boolean;
  showsPreview: boolean;
};

function createParagraph(text = ""): Paragraph {
  const id = String(Date.now());
  return {
    id,
    text,
  };
}
export function updateParagraphText(text: string, id: string) {
  return { type: "UPDATE_PARAGRAPH" as const, id, text };
}

export function addParagraph() {
  return { type: "ADD_PARAGRAPH" as const };
}

export function deleteParagraph(id: string) {
  return { type: "DELETE_PARAGRAPH" as const, id };
}

export function startMoving(index: number) {
  return { type: "MOVE_START" as const, index };
}
export function moveParagraph(from: number, to: number) {
  return { type: "MOVE_PARAGRAPH" as const, from, to };
}
export function endMove() {
  return { type: "END_MOVE" as const };
}

export function toggleSort() {
  return { type: "TOGGLE_SORT" as const };
}

export function togglePreview() {
  return { type: "TOGGLE_PREVIEW" as const };
}

export type EditorAction = ReturnType<
  | typeof updateParagraphText
  | typeof addParagraph
  | typeof deleteParagraph
  | typeof startMoving
  | typeof moveParagraph
  | typeof endMove
  | typeof toggleSort
  | typeof togglePreview
>;

export function editorReducer(
  state: EditorState,
  action: EditorAction
): EditorState {
  switch (action.type) {
    case "UPDATE_PARAGRAPH": {
      return {
        ...state,
        paragraphs: {
          ...state.paragraphs,
          [action.id]: {
            ...state.paragraphs[action.id],
            text: action.text,
          },
        },
      };
    }
    case "ADD_PARAGRAPH": {
      const p = createParagraph();
      return {
        ...state,
        paragraphs: {
          ...state.paragraphs,
          [p.id]: p,
        },
        list: [...state.list, p.id],
        lastAdded: state.list.length,
      };
    }
    case "DELETE_PARAGRAPH": {
      const paragraphs = state.paragraphs;
      delete paragraphs[action.id];
      state.list.splice(state.list.indexOf(action.id), 1);
      return {
        ...state,
        paragraphs: { ...paragraphs },
        list: [...state.list],
      };
    }
    case "MOVE_START": {
      return {
        ...state,
      };
    }
    case "MOVE_PARAGRAPH": {
      const from = state.list[action.from];
      const to = state.list[action.to];
      state.list[action.from] = to;
      state.list[action.to] = from;
      return {
        ...state,
        lastAdded: null,
        list: [...state.list],
      };
    }
    case "END_MOVE": {
      return {
        ...state,
      };
    }
    case "TOGGLE_SORT": {
      return {
        ...state,
        isSorting: !state.isSorting,
      };
    }
    case "TOGGLE_PREVIEW": {
      return {
        ...state,
        showsPreview: !state.showsPreview,
      };
    }
    default:
      return state;
  }
}

const initParagraph = createParagraph("");
export const initialState: EditorState = {
  ver: 1,
  paragraphs: {
    [initParagraph.id]: initParagraph,
  },
  list: [initParagraph.id],
  lastAdded: 0,
  isSorting: false,
  showsPreview: false,
};

const SAVE_KEY = "save_data";
export function saveEditorState(state: EditorState) {
  try {
    window.localStorage.setItem(SAVE_KEY, JSON.stringify(state));
  } catch {
    // nothing to do
  }
}

export function loadEditorState(): EditorState {
  try {
    const state: EditorState = JSON.parse(
      window.localStorage.getItem(SAVE_KEY) ?? ""
    );
    return {
      ...state,
      isSorting: false,
      lastAdded: null,
    };
  } catch {
    return initialState;
  }
}

export const EditorContext = createContext<{
  state: EditorState;
  dispatch: (action: EditorAction) => void;
}>({ state: initialState, dispatch: () => void 0 });
