import { h } from "preact";
import { Button } from "../../button/button";
import { AddBoxIcon } from "./addBoxIcon";
import style from "./addButton.css";
import buttonStyle from "../../button/button.css";
import { useCallback, useContext } from "preact/hooks";
import { addParagraph, EditorContext } from "../editorState";

export function AddButton() {
  const { dispatch } = useContext(EditorContext);
  const onClick = useCallback(() => {
    dispatch(addParagraph());
  }, []);
  return (
    <Button className={style["add-button"]} type="primary" onClick={onClick}>
      <AddBoxIcon className={buttonStyle["btn-icon"]} /> <span>追加する</span>
    </Button>
  );
}
