import { h } from "preact";
import { useCallback, useContext, useEffect, useRef } from "preact/hooks";
import cardStyle from "../../card/card.css";
import {
  deleteParagraph,
  EditorAction,
  EditorContext,
  moveParagraph,
  updateParagraphText,
} from "../editorState";
import { AllowDownwardIcon } from "./arrowDownwardIcon";
import { AllowUpwardIcon } from "./arrowUpwardIcon";
import { DeleteIcon } from "./deleteIcon";
import pStyle from "./paragraph.css";

interface ParagraphProps {
  id: string;

  index: number;
}

export function Paragraph(props: ParagraphProps) {
  const { state, dispatch } = useContext(EditorContext);
  const p = state.paragraphs[props.id];
  if (!p) {
    return null;
  }
  const bindDispatch = useCallback(function <
    A extends unknown[],
    B extends EditorAction
  >(fn: (...args: A) => B) {
    return useCallback((...args: A) => {
      dispatch(fn(...args));
    }, []);
  },
  []);
  const index = props.index;
  const isLastItem = index === state.list.length - 1;
  const onMoveUpward = useCallback(() => {
    dispatch(moveParagraph(index, index - 1));
  }, [index]);
  const onMoveDownward = useCallback(() => {
    dispatch(moveParagraph(index, index + 1));
  }, [index]);
  return (
    <ParagraphPresenter
      id={props.id}
      index={index}
      text={p.text}
      isSorting={state.isSorting}
      isLastItem={isLastItem}
      focus={index === state.lastAdded}
      onChange={bindDispatch(updateParagraphText)}
      onDelete={bindDispatch(deleteParagraph)}
      onMoveUpward={onMoveUpward}
      onMoveDownward={onMoveDownward}
    />
  );
}

interface ParagraphPresenterProps {
  id: string;
  index: number;
  text: string;
  isSorting: boolean;
  isLastItem: boolean;
  focus: boolean;
  onChange(text: string, id: string): void;
  onDelete(id: string): void;
  onMoveUpward(): void;
  onMoveDownward(): void;
}

export function ParagraphPresenter(props: ParagraphPresenterProps) {
  const onChange = useCallback(
    (e: Event) => {
      props.onChange?.((e.currentTarget as HTMLInputElement).value, props.id);
    },
    [props.onChange, props.id]
  );
  const onDelete = useCallback(
    (e: Event) => {
      e.preventDefault();
      props.onDelete?.(props.id);
    },
    [props.id]
  );
  const textArea = useRef<HTMLTextAreaElement | null>(null);

  useEffect(() => {
    if (props.focus) {
      textArea.current?.focus();
    }
  }, [props.focus]);
  useEffect(() => {
    if (textArea.current) {
      if (props.text.length === 0) {
        textArea.current.style.height = "";
      } else {
        textArea.current.style.height = `${
          textArea.current?.scrollHeight + 26
        }px`;
      }
    }
  }, [props.text]);
  const textAreaId = `paragraph-${props.index}`;
  const controlTabIndex = props.isSorting ? 0 : -1;
  return (
    <li
      className={`${cardStyle.card} ${pStyle.paragraph}`}
      data-is-editing={props.isSorting ? "true" : "false"}
    >
      <div className={`${pStyle.move} ${pStyle["edit-form"]}`}>
        <button
          className={pStyle["move-button"]}
          aria-label="上へ移動"
          tabIndex={controlTabIndex}
          onClick={props.onMoveUpward}
          disabled={props.index === 0}
        >
          <AllowUpwardIcon />
        </button>
        <button
          className={pStyle["move-button"]}
          aria-label="下へ移動"
          tabIndex={controlTabIndex}
          onClick={props.onMoveDownward}
          disabled={props.isLastItem}
        >
          <AllowDownwardIcon />
        </button>
      </div>
      <label className={pStyle.textarea}>
        <textarea
          id={textAreaId}
          name={`paragraph-${props.index}`}
          ref={textArea}
          value={props.text}
          onKeyUp={onChange}
          onChange={onChange}
          onPaste={onChange}
          placeholder={props.index === 0 ? "文章を入力" : ""}
        />
        <div className={pStyle.count} aria-label="文字カウント">
          {props.text.length}
        </div>
        <span className={pStyle["label-text"]}>段落</span>
      </label>
      <div className={`${pStyle.delete} ${pStyle["edit-form"]}`}>
        <button
          onClick={onDelete}
          aria-label="削除ボタン"
          tabIndex={controlTabIndex}
        >
          <DeleteIcon />
        </button>
      </div>
    </li>
  );
}
